//variables
$bgBlue: #008ad1
$bgGreenLight: #48b950
$bgGreenDark: #42ad49

$textLink: #0091de
$bgBlueMenu: #0aa5f5
$textGreyDark: #434a4d
$textGreyLight: #68767d

$general-transition: all .3s ease-in-out

$headerHeight: 63px

+font("roboto-light", roboto-light)
+font("roboto-regular", roboto-regular)
+font("roboto-bold", roboto-bold)
+font("raleway-light", raleway-light)
+font("raleway-semibold", raleway-semibold)

=h300
  font-family: "roboto-light", Helvetica, Arial, sans-serif

=h400
  font-family: "roboto-regular", Helvetica, Arial, sans-serif

=h700
  font-family: "roboto-bold", Helvetica, Arial, sans-serif

h1
  font-size: 28px
  color: $textGreyDark
  +h700

h2
  font-size: 22px
  color: $textGreyDark
  +h300

h3
  font-size: 18px
  color: $textGreyDark
  +h700

body
  display: flex
  flex-direction: column
  font-size: 13px
  line-height: 1.4
  color: $textGreyDark
  +h400

  +r(768)
    padding-top: $headerHeight

a
  display: inline-block
  text-decoration: none
  cursor: pointer
  color: $textGreyDark
  transition: $general-transition

  &:hover
    text-decoration: underline

img
  display: inline-block
  width: 100%

input[type='text'],
input[type='search']
  border-radius: 0
  -moz-appearance: none
  -webkit-appearance: none

button
  background-color: transparent

.mb-10
  margin-bottom: 10px

.i-mb-22
  margin-bottom: 22px

.i-bold
  +h700

.i-light
  +h300

.i-upper
  text-transform: uppercase

.atoms
  width: 1100px
  margin: 10px auto

.atom
  margin: 10px 0
  padding: 10px
  border: 1px solid #2c2c2c

// common blocks

.title

  &__subtitle
    +h300

.copyright

  &__descr
    font-size: 14px
    color: #fff
    +h300

.logo
  width: 170px

.banner
  margin-bottom: 15px

  &__link
    display: block

  &__name
    display: flex
    margin-bottom: 6px
    font-size: 10px
    color: $textGreyLight
    +h300

.discussion
  margin-top: 22px
  clear: both

  +r(1024)
    margin-bottom: 0

  .section-title
    margin-bottom: 5px

.section-title
  margin-bottom: 10px

.raiting
  margin-top: 5px
  display: flex
  flex-direction: row
  align-items: center

  &__text
    margin: 0 7px 5px 0

.block-info
  width: 560px
  display: flex
  flex-direction: column
  align-items: center
  margin: 0 auto

.category
  padding: 5px 0
  border-bottom: 1px solid #eaeaea
  transition: all .3s ease-in-out

.copyright
  font-size: 14px
  +h300

  +r(1024)
    font-size: 12px

.sidebar-head
  position: relative
  display: flex
  justify-content: space-between
  align-items: center
  border-bottom: 1px solid #e3e3e3

  &__title
    position: relative
    padding-bottom: 10px

    &:after
      position: absolute
      content: ''
      bottom: -1px
      left: 0
      width: 100%
      height: 2px
      background-color: $textLink