.social-likes, .social-likes__widget
  display: inline-block
  padding: 0
  vertical-align: middle !important
  word-spacing: 0 !important
  text-indent: 0 !important
  list-style: none !important

.social-likes
  opacity: 0

.social-likes_visible
  opacity: 1
  transition: opacity .1s ease-in

.social-likes > *
  display: inline-block
  visibility: hidden

.social-likes_vertical > *
  display: block

.social-likes_visible > *
  visibility: inherit

.social-likes__widget
  display: inline-block
  position: relative
  white-space: nowrap
  &:before, &:after
    display: none !important

.social-likes_vertical .social-likes__widget
  display: block
  float: left
  clear: left

.social-likes__button, .social-likes__icon, .social-likes__counter
  text-decoration: none
  text-rendering: optimizeLegibility

.social-likes__button, .social-likes__counter
  display: inline-block
  margin: 0
  outline: 0

.social-likes__button
  position: relative
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none
  &:before
    content: ""
    display: inline-block

.social-likes__icon
  position: absolute
  top: 0
  left: 0

.social-likes__counter
  display: none
  position: relative

.social-likes_ready .social-likes__counter, .social-likes__counter_single
  display: inline-block

.social-likes_ready .social-likes__counter_empty
  display: none

.social-likes_vertical .social-likes__widget
  display: block

.social-likes_notext .social-likes__button
  padding-left: 0

.social-likes_single-w
  position: relative
  display: inline-block

.social-likes_single
  position: absolute
  text-align: left
  z-index: 99999
  visibility: hidden
  opacity: 0
  transition: visibility 0s .11s,opacity .1s ease-in
  -webkit-backface-visibility: hidden
  backface-visibility: hidden
  &.social-likes_opened
    visibility: visible
    opacity: 1
    transition: opacity .15s ease-out

.social-likes__button_single
  position: relative

.social-likes
  min-height: 36px
  margin: -6px
  line-height: 19px

.social-likes_single-w
  line-height: 19px

.social-likes__widget
  margin: 6px
  color: #000
  background: #fff
  border: 1px solid #ccc
  border-radius: 3px
  line-height: 19px
  &:hover, &:active, &:focus
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    color: #fff
    cursor: pointer

.social-likes__button, .social-likes__counter
  vertical-align: middle
  font-family: "Helvetica Neue",Arial,sans-serif
  font-size: 13px
  line-height: 20px
  cursor: inherit

.social-likes__button
  padding: 1px 6px 1px 20px
  font-weight: 400
  border-right: 0

.social-likes__icon
  width: 20px
  height: 18px
  margin-top: 1px
  background-repeat: no-repeat
  background-position: 50% 50%

.social-likes__counter
  min-width: 12px
  padding: 1px 4px
  font-weight: 400
  text-align: center
  border-left: 1px solid #ccc

.social-likes__widget
  &:hover > .social-likes__counter, &:focus > .social-likes__counter, &:active > .social-likes__counter
    position: relative
    border-left-color: transparent

.social-likes__widget_active > .social-likes__counter
  position: relative
  border-left-color: transparent

.social-likes__widget
  &:hover > .social-likes__counter:before, &:focus > .social-likes__counter:before, &:active > .social-likes__counter:before
    content: ""
    position: absolute
    top: 0
    bottom: 0
    left: -1px
    border-left: 1px solid
    opacity: .4

.social-likes__widget_active > .social-likes__counter:before
  content: ""
  position: absolute
  top: 0
  bottom: 0
  left: -1px
  border-left: 1px solid
  opacity: .4

.social-likes_vertical
  margin: -6px
  .social-likes__widget
    margin: 6px

.social-likes_notext
  margin: -3px
  .social-likes__widget
    margin: 3px
  .social-likes__button
    width: 18px
  .social-likes__icon
    width: 100%
    background-position: center center

.social-likes__widget_single
  margin: 0

.social-likes_single
  margin: -12px 0 0
  padding: 4px 0
  background: #fff
  border: 1px solid #ccc
  border-radius: 3px

.social-likes__single-container
  width: 100%
  display: table

.social-likes_single
  .social-likes__widget
    float: none
    display: table-row
    margin: 0
    border: 0
    border-radius: 0
    background: 0 0
  .social-likes__button
    display: block
    padding: 2px 6px 2px 24px
  .social-likes__icon
    margin-left: 4px
    margin-top: 2px
  .social-likes__counter
    display: table-cell
    padding: 2px 8px 2px 4px
    border-left: 0
    text-align: right
  .social-likes__widget
    &:hover > .social-likes__counter:before, &:focus > .social-likes__counter:before, &:active > .social-likes__counter:before
      display: none

